@import url(https://fonts.googleapis.com/css2?family=Lato&family=Playfair+Display&display=swap);
body {
  text-align: center;
  color: #4d4d4d;
  margin: 0;
  font-family: 'Playfair Display', serif;
  font-weight: 300;
  background: #ebeae5; }

h1, h2, h3, h4, h5, h6, p, a {
  padding: 0;
  margin: 0;
  font-weight: 300; }

p {
  font-family: 'Lato', sans-serif; }

a {
  color: black;
  -webkit-transition: 0.5s color;
  transition: 0.5s color; }
  a:hover {
    color: #ad8305; }

button {
  background: none;
  border: none;
  outline: none;
  color: black;
  font-size: 16px;
  font-family: 'Playfair Display', serif; }

ul {
  padding: 0; }

ul li {
  display: inline-block;
  list-style: none;
  margin-bottom: 3px;
  padding: 0; }

a, p, button {
  text-decoration: none; }

.fa {
  font-size: 16px;
  -webkit-transition: 0.5s color;
  transition: 0.5s color; }
  .fa:hover {
    color: #ad8305; }

.highlight {
  color: #ad8305;
  font-weight: bold; }

.container {
  max-width: 900px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 20px; }

.profile {
  padding-bottom: 1rem; }
  .profile .profile-info {
    border-bottom: 1px solid gainsboro;
    padding-bottom: 10px; }
  .profile ul {
    margin: 0.5rem 0 0 0;
    padding: 0;
    align-self: flex-end; }
  .profile li {
    margin-right: 10px; }

.projects-nav p {
  font-family: 'Playfair Display', serif; }

.projects-nav ul li {
  cursor: pointer;
  padding: 3px 0;
  margin-right: 15px; }
  .projects-nav ul li button {
    cursor: pointer;
    padding: 0;
    -webkit-transition: 0.5s color;
    transition: 0.5s color;
    font-size: 13px; }
    .projects-nav ul li button:hover {
      color: #ad8305; }

.project {
  min-height: 548px; }
  .project .project-name .project-mobile-screenshot {
    display: none; }
  .project .project-name.hide {
    display: none; }
  .project .project-detail {
    position: relative;
    margin-top: 20px;
    background: white;
    padding: 35px; }
    .project .project-detail li {
      margin-right: 5px;
      font-weight: bold; }
  .project .project-desc {
    line-height: 1.6;
    padding-bottom: 2rem; }
  .project img {
    width: 100%; }
  .project .skill span {
    font-size: 13px; }
  .project .skill:after {
    content: '/';
    display: inline-block;
    height: 5px;
    color: #adadad; }
  .project .skill:last-child:after {
    content: ''; }
  .project .skill-item {
    font-size: 12px;
    display: inline;
    color: #919090;
    font-weight: bold;
    padding: 2px 6px 0 0;
    text-transform: uppercase;
    border-radius: 3px; }
  .project .button-group a {
    margin-right: 20px; }
  .project .button-group .demo {
    padding: 5px 20px;
    background: black;
    color: white;
    -webkit-transition: .5s background;
    transition: .5s background; }
    .project .button-group .demo:hover {
      background: #ad8305; }
  .project .bio {
    text-align: left;
    text-align: left;
    padding: 30px;
    background: white; }
    .project .bio .bio-skills li {
      display: block;
      margin-bottom: 0; }
    .project .bio .bio-skills p {
      margin: 0; }
    .project .bio p {
      line-height: 1.6;
      margin-bottom: 1rem; }

footer img {
  height: 20px;
  width: auto;
  margin-right: 5px; }

.bio {
  margin-top: 20px; }
  .bio .about-me {
    margin-bottom: 20px; }

.bio-inner img {
  display: block;
  margin: 0 auto;
  margin-bottom: 30px;
  height: 350px;
  width: auto; }

@media only screen and (min-width: 800px) {
  body {
    text-align: left;
    text-align: initial;
    padding: 50px; }
  .project {
    min-height: 430px; }
  .profile .profile-info {
    display: flex;
    justify-content: space-between;
    flex-grow: 1; }
  .project .project-name {
    align-items: center; }
    .project .project-name .project-images {
      display: flex; }
      .project .project-name .project-images .project-desktop-screenshot,
      .project .project-name .project-images .project-mobile-screenshot {
        height: 356px;
        width: auto; }
      .project .project-name .project-images .project-mobile-screenshot {
        margin-left: 26px;
        display: inline-block; }
  .projects-nav ul {
    display: flex;
    justify-content: center; }
    .projects-nav ul li:last-child:after {
      content: ''; }
  .bio-inner {
    display: flex; }
    .bio-inner img {
      margin-right: 30px; } }

